.slick-slide img {
  margin: auto;
}

@media (max-width: 700px) {
    .welcome_cards_warpp {
        padding: 0rem 1rem;
    }

    .slick-prev {
        left: -16px !important;
    /* background: #0d89f6 !important; */
    z-index: 10 !important;
    }
    .slick-next {
        right: -10px !important;
    /* background: #0d89f6 !important; */
    z-index: 10 !important;
    }

    .slick-prev:before, .slick-next:before {
        color: gray !important;
        font-size: 25px !important;
    }
}