.introd-main {
    width: 100%;
    height: 100%;
    margin-top: 3rem;
    margin-bottom: 3rem;
    max-width: 1140px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    /* padding: 0 var(--space); */
}

.introd-bgimgbox {
    width: 100%;
    height: 225px;
    position: relative;
}

.introd-bg {
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
}

.introd-bg-contbox {
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 1rem;
    padding-top: 2rem;
}

.introd-bg-head {
    font-size: 1.3rem;
    font-weight: 700;
    color: var(--color-black);
}

.introd-bg-contbox {
    width: 315px;
}

.introd-bg-txt {
    line-height: 1;
}

.introd-bg-subtxt {
    text-transform: uppercase;
    font-weight: 800;
    font-size: 0.8rem;
}

.introd-cardbox {
    display: flex;
    gap: 5px;
}

.introd-card1 {
    width: 33%;
    background-color: #00031C;
    display: flex;
    justify-content: center;
    align-items: center;
}

.introd-card2 {
    width: 33%;
    background-color: #4B3BBF;
    display: flex;
    justify-content: center;
    align-items: center;
}

.introd-card3 {
    width: 33%;
    height: 200px;
    background-color: var(--color-orange);
    display: flex;
    justify-content: center;
    align-items: center;
}

.introd-card-txt {
    font-size: 18px;
    font-weight: 800;
    color: var(--color-white);
}

.introd-card-innerbox {
    /* width: 200px; */
    /* padding-left: ; */
}

.introd-contbox {
    max-width: 50vw;
    margin-top: 2rem;
    margin-left: 2rem;
}

.introd-contbox-head {
    color: var(--color-black);
    font-weight: 800;
    font-size: 16px;
}

.introd-contbox-txt {
    color: var(--color-black);
    font-weight: 800;
    font-size: 16px;
}

.introd-contbox-txt {
    color: var(--color-black);
    font-size: 16px;
    font-weight: 400;
}


@media screen and (max-width: 810px) {


    .introd-card-txt {
        padding-left: 15px;
        padding-right: 15px;
    }

    .introd-contbox {
        min-width: 100%;
    }
}

@media screen and (max-width: 600px) {


    .introd-contbox {
        max-width: 100vw;
        margin-right: 2rem;
    }

    .introd-contbox-txt {
        margin-right: 2rem;
    }

    .introd-cardbox {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .introd-card1 {
        min-width: 330px;
        height: 220px;
    }

    .introd-card2 {
        min-width: 330px;
        height: 220px;
    }

    .introd-card3 {
        min-width: 330px;
        height: 220px;
    }


}

/* Modal Open Media Query */

@media screen and (max-width:420px) {
    .home_model_4wrapp {
        width: 90vw;
    }
}