/* Profile sidebar design */

.profile_side_wrapp {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background: #12192a;
    width: 100%;
    height: 100%;
    padding: 1rem 2rem;
    gap: 2rem;
}

.profile_side_sec1 {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.profile_side_sec1>img {
    width: 68px;
    height: 68px;
    border-radius: 68px;
    object-fit: cover;
    overflow: hidden;
}

.profile_side_sec1_username>h4 {
    font-size: 1rem;
    font-weight: bold;
    color: var(--color-white);
}

.profile_side_sec1_username>p {
    font-size: 1rem;
    font-weight: 200;
    color: var(--color-white);
}

.profile_side_sec2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 1rem;
}

.profile_side_sec2>a {
    cursor: pointer;
    font-size: 1rem;
    color: var(--color-white);
}

/* My Profile Section style */

.myprofile_sec_main_wrapp {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    margin: 1rem auto;
}

.myprofile_inner_sec1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 1rem;
    width: 60%;
}

.myprofile_inner_sec2 {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    width: 35%;
    height: 100%;
    padding: 1rem;
    border: 1px solid #000;
}

.myprofile_inner_sec1_sigle_block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #000;
}

.myprofile_inner_sec1_sigle_block>div {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;

}

.myprofile_inner_sec1_sigle_block>div>p {
    font-size: 1rem;
    font-weight: 400;
    color: var(--color-black);
}

.myprofile_inner_sec1_sigle_block>div>h4 {
    font-size: 1rem;
    font-weight: 700;
    color: var(--color-black);
}

.myprofile_inner_sec1_sigle_block>button {
    background: var(--color-orange);
    color: var(--color-white);
    font-size: 0.9rem;
    font-weight: 700;
    width: 100px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 0.5rem 2rem; */
    cursor: pointer;
}

.myprofile_inner_sec2>h4 {
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 1rem;
}

.myprofile_inner_sec2_img_upload {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px dashed var(--color-gray);
    background: #f6e9e1;
    padding: 10px;
    margin-bottom: 1rem;
}

.myprofile_inner_sec2_img_upload>h4 {
    font-weight: bold;
    font-size: 1rem;
    color: var(--color-dark-gray);
    margin-bottom: 10px;
}

.myprofile_inner_sec2_img_upload>P {
    font-weight: 400;
    font-size: 1rem;
    color: var(--color-gray);
    margin-bottom: 10px;
}

.myprofile_inner_sec2_img_upload>a {
    font-weight: 400;
    font-size: 1rem;
    color: var(--color-blue);
}

.myprofile_inner_sec2_img_upload>a:hover {
    text-decoration: underline;
    cursor: pointer;
}

.myprofile_upload_img_btn_wrapp {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 8px;
}

.myprofile_upload_img_btn_wrapp>button {
    /* background: var(--color-orange);
    color: var(--color-white); */
    font-size: 0.9rem;
    font-weight: 700;
    width: 100px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 0.5rem 2rem; */
    cursor: pointer;
}

/* section - 2 */

.candc_main_wrapp {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    margin: 1rem auto;
}

.candc_single_wrapp {
    width: 80%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    column-gap: 10px;
    border: 1px solid black;
    padding: 5px;
}

.candc_single_wrapp>img {
    width: 100%;
    height: 100%;
}

.candc_single_text_wrapp {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 4px;
}

.candc_single_text_wrapp>div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.candc_single_text_wrapp>div>p {
    font-size: 1rem;
    font-weight: 500;
    font-weight: bold;

}

.candc_single_text_sec1>p {
    font-size: 1rem;
    font-weight: 800 !important;
    color: var(--color-black);
}

.candc_single_text_sec2>button {
    font-weight: 800;
    font-size: 1rem;
    padding: 10px;
}

.candc_single_text_sec3>p>span {
    font-size: 1rem;
    font-weight: 400;
}

.candc_single_text_sec5 {
    font-size: 1rem;
    font-weight: 900;
    color: var(--color-black);
}

.candc_single_text_sec4 {
    font-size: 14px;
}

.candc_single_text_sec3 {
    justify-content: flex-start !important;
    gap: 1rem;
}

.candc_single_text_sec6 {
    width: 60% !important;
    align-self: self-start;
}

.candc_single_text_sec6>p {
    font-weight: 400 !important;
    line-height: normal;
}

/* --------- custom dropdown start --------- */
.custom-select {
    /* A reset of styles, including removing the default dropdown arrow */
    appearance: none;
    /* Additional resets for further consistency */
    background-color: #f9f2dd;
    border: none;
    padding: 2px 10px 2px 10px;
    margin: 0;
    width: auto;
    font-family: inherit;
    font-size: 1rem;
    cursor: inherit;
    line-height: inherit;
    cursor: pointer;
}

.custom-select::-ms-expand {
    display: none;
}

.custom-select {
    /* ...existing styles */
    outline: none;
}

/* --------- custom dropdown end --------- */

/* section - 3 */

.wishlist_payment_wrapp {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.wishlist_payment_wrapp>p {
    font-size: 12px;
}

.wishlist_payment_wrapp>button {
    cursor: pointer;
}

/* section - 5 */

.order_history_sigle_wrapp {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    height: 100%;
    max-width: var(--max-width);
    padding: 5px;
    border: 1px solid #000;
    align-self: flex-start;
    gap: 5px;
}

.order_history_single_sec1,
.order_history_single_sec2 {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.order_history_single_sec1>p {
    font-size: 1rem;
    font-weight: 700;
}

.order_history_single_sec2>p {
    font-size: 1rem;
    /* margin-top: -10px; */
}

.order_history_single_sec2>button {
    font-size: 14px;
    padding: 10px;
    font-weight: 700;
}

.order_history_single_sec3 {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    grid-gap: 10px;
    width: 80%;
}

.order_history_single_sec3_item {
    width: 100%;
    height: 100%;
}


/* last add 14-4 */

.btnn-main {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
}

.btn-edit {
    font-size: 0.9rem;
    font-weight: 700;
    width: 100px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 0.5rem 2rem; */
    cursor: pointer;
}

/* Media Query */

/* My Profile */

@media screen and (max-width: 1030px) {
    .myprofile_sec_main_wrapp {
        flex-direction: column-reverse;
        gap: 25px;
    }

    .myprofile_inner_sec1 {
        width: 100%;
    }

    .myprofile_inner_sec2 {
        width: 100%;
    }
}

/* Click & Collect */
@media screen and (max-width:1100px) {
    .candc_single_wrapp {
        grid-template-columns: 1fr;
        width: 100%;
    }



}

@media screen and (max-width:420px) {
    .candc_single_text_wrapp>div>p {
        width: 70%;
        font-size: 0.8rem;
    }

    .candc_single_text_sec2>button {
        width: 30%;
        font-size: 0.7rem;
        padding: 8px;
    }

    .custom-select {
        font-size: 0.8rem
    }

    .candc_single_text_sec4 {
        font-size: 12px;
    }
}

/* My Wishlist */

@media screen and (max-width:975px) {
    .mywish-flex-col {
        flex-direction: column;
    }
}

/* Order History Media Query*/

@media screen and (max-width:420px) {
    .order_history_single_sec2 {
        flex-direction: column;
        gap: 5px;
    }

    .order_history_single_sec2>button {
        width: 50%;
    }
}