.nav_main_wrapp {
    display:flex;
    align-items: center;
    background: var(--color-white);
    width: 100%;
    min-width: 100vw;
    gap: 15%;
    position: sticky;
    top: 0px;
    left: 0px;
    z-index: 11;
}

.nav_base_wrapp {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.2rem 1rem;
    width: 100%;
}

.nav_logo {
    max-width: 200px;
    width: 100%;
    height: auto;
}

.nav_links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.2rem;
}

.nav_links > a {
    color: var(--color-black);
    font-weight: 400;
    font-size: 1rem;
}

.nav_myacc_wrapp {
        display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-left: 1rem;
}

.nav_profile {
    width: 26.5px;
    height: 25.5px;
}

.sidebar_logo {
    display: none;
}

.nav_sidebar_wrapp {
    width: 100%;
    background: var(--color-white);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding: 10px 0px; */
}

.nav_sidebar_wrapp > a {
    padding: 1rem;
    border-bottom: 1px solid #C1C1C1;
    width: 100%;
    font-size: 18px;
    font-weight: 300;
    text-align: right;
    color: var(--color-black);
    cursor: pointer;
    /* background: var(--color-orange); */
}

.nav_sidebar_wrapp > a:hover{
    font-weight: bold;
    color: var(--color-white);
    background: var(--color-orange);
}

.accunt_sec_wrapp {
    width: 100%;
    background: var(--color-white);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px 0px;
}


.accunt_sec_wrapp > a {
    padding: 1rem;
    width: 100%;
    font-size: 16px;
    font-weight: 300;
    text-align: right;
    color: var(--color-black);
    cursor: pointer;
    /* background: var(--color-orange); */
}

.accunt_sec_wrapp > a:hover {
    font-weight: bold;
    color: var(--color-orange);
}

@media (max-width: 900px) {
    .sidebar_logo {
    display: block;
}

.nav_links {
    display: none;

}
}

