.introd-links-line {
    width: 1px;
    height: 20px;
    background-color: var(--color-dark-gray);
}

.introd-links-flex {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-top: 1rem;
    margin-left: 1rem;
}

.introd-link {
    font-weight: 600;
    color: var(--color-dark-gray);
}

.introd-content-flex {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-left: 1rem;
}

.introd-cotent-txt {
    font-size: 14px;
    min-width: 100%;
    text-align: justify;
}

@media screen and (max-width:1260px) {
    .introd-link {
        font-size: 14px;
        font-weight: 800;
    }

}

@media screen and (max-width:1135px) {
    .introd-link {
        font-size: 12px;
        font-weight: 800;
    }

    .introd-links-flex {
        gap: 7px;
    }

}

@media screen and (max-width:690px) {

    .introd-links-flex {
        flex-wrap: wrap;
    }

}