:root {
  --space: 2rem;
  --space-xs: calc(var(--space) / 3);
  --space-sm: calc(var(--space) / 2);
  --space-md: calc(var(--space) * 2);

  --color-primary: lightgray;
  --color-accent: whitesmoke;
  --color-dark: black;
  --color-mid: gray;
  --color-light: white;
  --color-highlight: dodgerblue;

  --radius: 0.125rem;
}

/* Here are the base styles for the main layout and sticky component */
* {
  box-sizing: border-box;
}

main {
  display: flex;
  flex-wrap: wrap;
}

.article {
  flex-basis: 0;
  flex-grow: 999;
  min-width: 40%;
}

.sidebar {
  --offset: var(--space);
  flex-grow: 1;
  flex-basis: 300px;
  align-self: start;
  position: sticky;
  top: var(--offset);
}

.component {
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.component .content {
  max-height: 500px;
  overflow-y: auto;
}

.sidebar .component {
  max-height: calc(100vh - var(--offset) * 2);
}

/*
 * Other styles
**/
.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

html,
body {
  height: 100%;
}

[hidden] {
  display: none;
}

.ps_main_wrapp {
  margin: 3rem auto;
  padding: 0 var(--space);
  /* font-family: "Helvetica Neue", sans-serif; */
  /* font-size: 1.25rem; */
  /* line-height: 1.4; */
  /* max-width: var(--max-width); */
  min-height: 71vh;
}

/* Until flexbox gap is supported in all modern browsers, we use the negative margin trick to create space between the children elements */
main {
  margin-bottom: calc(var(--space-md) * -1);
  margin-left: calc(var(--space-md) * -1);
}

main > * {
  margin-bottom: var(--space-md);
  margin-left: var(--space-md);
}

article > * + * {
  /* margin-top: var(--space); */
  margin-top: 10px;
}

.component {
  position: relative;
  border: 1px solid var(--color-primary);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.04),
    0 4px 8px rgba(0, 0, 0, 0.03), 0 8px 16px rgba(0, 0, 0, 0.03),
    0 16px 32px rgba(0, 0, 0, 0.02), 0 32px 64px rgba(0, 0, 0, 0.02);
}

.component .header,
.component .footer {
  padding: var(--space-sm);
  text-align: center;
}

.component .header {
  border-bottom: inherit;
}

.component .footer {
  display: flex;
  justify-content: space-between;
  border-top: inherit;
}

.component .content {
  padding: var(--space-sm);
  max-height: 500px;
  overflow-y: auto;
  color: var(--color-dark);
}

.empty-text {
  padding: var(--space);
  text-align: center;
  color: var(--color-primary);
}

.item {
  position: relative;
  appearance: none;
  display: flex;
  align-items: center;
  /* padding: var(--space); */
  outline: none;
  width: 100%;
  /* he */
  background-color: var(--color-accent);
  border: 1px solid var(--color-accent);
  border-radius: var(--radius);
  cursor: pointer;
  /* max-width: 250px; */
}

.item > img {
  width: 100%;
  object-fit: cover;
  height: 200px;
}

.item:active {
  transform: translateY(1px);
}

.item:focus {
  outline: 2px solid var(--color-highlight);
}

.item > * {
  pointer-events: none;
}

.info {
  width: 100%;
}

.title,
.subtitle {
  width: 100%;
  height: 0.5rem;
  background-color: var(--color-primary);
  border-radius: var(--radius);
}

.title {
  margin-bottom: var(--space-xs);
  max-width: 15ch;
}

.subtitle {
  max-width: 12ch;
}

.thumbnail {
  flex-shrink: 0;
  width: var(--size);
  height: var(--size);
  background: var(--color-primary);
  border-radius: var(--radius);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 10px;
}

.grid-poster {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr, 1fr));
  grid-gap: var(--space-sm);
}

.grid .item {
  flex-direction: column;
  text-align: center;
  height: 200px;
}

.grid-poster .item {
  flex-direction: column;
  text-align: center;
}

.grid .info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.grid .thumbnail {
  --size: 100px;
  margin-bottom: var(--space);
}

.list > * + * {
  margin-top: var(--space-xs);
}

.list .item {
  padding: var(--space-sm);
}

.list .item::before {
  content: "×";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: var(--space-sm);
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05rem;
  color: var(--color-primary);
  pointer-events: none;
}

.list .thumbnail {
  --size: 50px;
  margin-right: var(--space-sm);
}

.button {
  appearance: none;
  padding: var(--space-xs) var(--space-sm);
  font-family: inherit;
  font-size: 1rem;
  color: white;
  background-color: var(--color-dark);
  border: 1px solid var(--color-dark);
  cursor: pointer;
  border-radius: var(--radius);
  box-shadow: rgba(black, 0.1) 4px 4px;
}

.button:active {
  transform: translateY(1px);
  box-shadow: none;
}

.empty-cart {
  color: var(--color-mid);
  border-color: var(--color-mid);
  background-color: var(--color-light);
}

/* sidebar */

.sidebar_img {
  width: 100%;
  height: 200px;
  position: relative;
}

.sidebar_img > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.sidebar_img_footer {
  position: absolute;
  bottom: 0px;
  right: 0px;
  width: 100%;
  /* height: 1.5rem; */
  padding: 0.3rem 0.6rem;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sidebar_img_footer > p {
  font-size: 14px;
  font-weight: 600;
}

.sidebar_tabs_wrapp {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;

  width: 100%;
  height: 100%;

  margin-top: 10px;
  gap: 3px;
}

.sidebar_tabs_wrapp > button {
  background: var(--color-orange2);
  color: var(--color-white);
  font-size: 1.2rem;
  font-weight: 700;
  padding: 0.5rem 1rem;
  width: 100%;
}

.sidebar_tabs_wrapp > button:hover {
  background: var(--color-orange);
  cursor: pointer;
}

/* secound tab style */

.grid2 .item2 {
  flex-direction: column;
  text-align: center;
  /* height: 350px; */
}

.list .item2 {
  padding: var(--space-sm);
}

.grid2 {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 10px;
}

/* Thired tab style */

.grid3 .item3 {
  flex-direction: column;
  text-align: center;
  /* height: 350px; */
}

.list .item3 {
  padding: var(--space-sm);
}

.grid3 {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 10px;
}

.item3 {
  position: relative;
  appearance: none;
  display: flex;
  align-items: center;
  /* padding: var(--space); */
  outline: none;
  width: 100%;
  /* he */
  background-color: var(--color-accent);
  border: 1px solid var(--color-black);
  border-radius: var(--radius);
  cursor: pointer;
  /* max-width: 250px; */
}

.item3 > img {
  width: 100%;
  object-fit: contain;
  height: 130px;
}

/* Eteries Model Design */

.eteries_model_wrapp {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.eteries_model_sec1 {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-template-rows: 1fr auto;
  width: 100%;
  /* gap: 5%; */
  column-gap: 1rem;
  row-gap: 1.5rem;
}

.eteries_model_sec1 > img {
  width: 100%;
  height: 100%;
}

.eteries_model_sec1_text_wrapp > p {
  font-size: 18px;
  font-weight: 500;
}
.eteries_model_sec1_text_wrapp > p:nth-child(3) {
  margin-bottom: 1rem;
}

.eteries_model_sec2 {
  grid-column: 1 / -1;
  font-size: 1rem;
  /* width: 100%; */
}

/* section - 5 */

.event_main_wrapp {
  width: 80%;
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1rem;
}

.event_single_inner_text_wrapp {
    display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

.event_single_wrapp > img {
  width: 100%;
  height: auto;
}

.event_single_wrapp {
 
    display: grid;
    gap: 14px;
    width: 100%;
    grid-template-columns: 1fr 2fr;

}

.event_single_inner_text_wrapp > p {
  margin-bottom: 10px;
  font-size: 16px;

}

.event_single_inner_text_wrapp > h4 {
  font-size: 18px;
  font-weight: 600;
}

.event_single_inner_text_wrapp > h6 {
  font-size: 17px;
  margin-bottom: 10px;
}
.event_single_span {
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: 600;
}

/* section - 6  */

.facilites_grid_wrapp {
    display: grid;
  /* grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr; */
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  width: 100%;
  grid-gap: 1rem;
}

.facilites_grid_item {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   justify-content: center;
   text-align: left;
   gap: 12px;
}

.facilites_grid_item > p {
  /* color: var(--color-mid); */
  font-size: 1rem;
}
.facilites_hr {
  background: var(--color-black);
  height: 1px;
  width: 100%;
}


/* section - 7 */

.contactus_grid_wrapp {
  width: 60%;
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1rem;
}

/* media queary */

@media (max-width: 778px) {
  main {
    flex-direction: column-reverse;
  }

  .eteries_model_sec1 {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    column-gap: 0rem;
    row-gap: 1rem;
  }
}
