.wc_card_main_wrapp {
    width: 379px;
    height: 365px;
    border-top-right-radius: 26px;
    position: relative;
    margin: 0 auto;
}

.wc_card_main_wrapp {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 360px;
}

.wc_bottom_img {
    position: absolute;
    bottom: 0px;
    right: 0px;
    max-width: 135.23px;
    max-height: 81.26px;
}

.wc_top_dot {
    position: absolute;
    width: 33px;
    height: 33px;
    border-radius: 33px;
    background: var(--color-white);
    top: 21px;
    right: 23px;
}

.wc_text_wrapp {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: left;
    color: var(--color-white);
    width: 80%;
    margin: auto;
}

.wc_text_wrapp {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: left;
    color: var(--color-white);
    width: 80%;
    margin: auto;
}
