.mt-10 {
    margin-top: 0px;
}

.uniq-header {
    height: 60px;
    background-color: var(--color-orange);
    display: flex;
    align-items: center;
}

.uniq-header-txt {
    padding-left: 1rem;
    color: var(--color-white);
    font-weight: 800;
    font-size: 1.5rem;
}

.uniq-part-main {
    display: flex;
    gap: 1.5rem;
}

.uniq-part1 {
    max-width: 40%;
}

.uniq-part2 {
    max-width: 60%;
    display: grid;
    grid-template-columns: repeat(3, minmax(100px, 1fr));
    /* grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); */
    /* grid-template-rows: auto auto; */
    gap: 10px;
}

.uniq-main-txt {
    font-size: 16px;
    font-weight: 800;
}

.uniq-txt {
    font-size: 16px;
    margin-bottom: 12px;
}

.uniq-imgbox>img {
    width: 100%;
    height: 100%;
    /* object-fit: contain; */
}

.uniq-imgbox {
    position: relative;
}

.uniq-contentbox-main {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #0000006c;
}

.uniq-contentbox-inn {
    padding: 20px;
    background-color: #ffffffab;
}

.uniq-imgbox-txt {
    font-size: 16px;
    font-weight: 600;
}

@media screen and (max-width: 1660px) {
    .uniq-imgbox-txt {
        font-size: 12px;
    }
}

@media screen and (max-width: 1485px) {
    .uniq-part2 {

        grid-template-columns: repeat(3, minmax(100px, 1fr));
        max-width: 100%;
    }

    .uniq-part1 {
        max-width: 100%;
    }



    .uniq-part-main {
        flex-direction: column;
    }

    .uniq-txt {
        max-width: 100%;
    }
}


/* About Inn Store */

.about-card1 {
    width: 33%;
    background-color: #00031C;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-card2 {
    width: 33%;
    background-color: #4B3BBF;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-card3 {
    width: 33%;
    height: 200px;
    background-color: var(--color-orange);
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-cardbox {
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.about-card-height {
    height: 185px;
    width: 400px;
}

.about-card-txt {
    font-size: 22px;
}

.about-main-flex {
    display: flex;
    gap: 1rem;
}

.about-phne-imgbox {
    max-width: 200px;
}

.about-phne-imgbox>img {
    width: 100%;
    height: 100%;
}

/* About In Store media query */

@media screen and (max-width: 530px) {
    .about-cardbox {
        width: 100%;
    }

    .about-main-flex {
        align-items: start !important;
    }

    .about-card-height {
        width: 285px !important;
        height: 185px;
    }

}

