/* event section update details */

.update-details-card {
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    max-width: 1140px;
    background-color: var(--color-white);
    /* height: 500px; */
    padding: 20px;
    display: flex;
    gap: 3%
}

.update-details-part1 {
    width: 27%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.update-details-part2 {
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.update-details-txt {
    font-size: 16px;
    font-weight: 800;
}

/* .upload-box-width {
    width: 250px !important;
    height: 250px;
} */

.update-details-last-inp-flx {
    display: flex;
    gap: 1%
}

.update-details-inp {
    padding: 10px 5px 10px 5px;
    width: 100%;
}

.inp-height {
    height: 125px;
}

.update-details-btn-flex {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 30%;
}

.last-inp-width {
    width: 78%;
    align-self: start;
}

/* Media Query */


@media screen and (max-width: 1130px) {
    .update-details-card {
        max-width: 100%;
    }

    .last-inp-width {
        width: 100%;
    }

    .update-details-last-inp-flx {
        flex-direction: column;
        gap: 10px;
    }

    .update-details-btn-flex {
        width: 100%;
    }
}


@media screen and (max-width: 975px) {
    .update-details-card {
        flex-direction: column;
    }

    .update-details-part1 {
        width: 100%;
    }

    .update-details-part2 {
        width: 100%;
    }
}

/* Store Direcory strat */


.store-directory-card {
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    max-width: 1140px;
    background-color: var(--color-white);
    /* height: 500px; */
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.store-directory-part1 {
    display: flex;
    gap: 5%;
}

.store-directory-first-inner-part1 {
    width: 75%;
}

.store-directory-first-inner-part2 {
    width: 30%;
    align-self: flex-end;
}

.store-directory-part2 {
    width: 100%;

}

.store-dire-head {
    font-weight: 800;
    font-size: 16px;
    margin-bottom: 20px;
}

.fontsize-btn {
    font-size: 14px;
}

.store-directory-part3 {
    display: flex;
    justify-content: space-between;
}

.store-directory-third-inner-part1 {
    width: 30%;
}

.store-directory-third-inner-part2 {
    width: 30%;
}

.mb-10 {
    margin-bottom: 10px;
}






/* Store Directory Media Query */






@media screen and (max-width: 1175px) {
    .store-directory-part1 {
        flex-direction: column;
        gap: 10px;
    }

    .store-directory-first-inner-part2 {
        align-self: flex-start;
        width: 100%;
    }
}

@media screen and (max-width: 930px) {
    .store-directory-part3 {
        flex-direction: column;
        gap: 10px;
    }

    .store-directory-third-inner-part1 {
        width: 100%;
    }

    .store-directory-third-inner-part2 {
        width: 100%;
    }

    .store-directory-third-inner-part3 {
        width: 100%;
    }
}




/*  */
@media screen and (max-width: 1030px) {
    .uniq-part2 {

        grid-template-columns: repeat(2, minmax(100px, 1fr));
        max-width: 100%;
    }

    /* About In Store section */

    .about-main-flex {
        flex-direction: column;
        align-items: center;
    }
}


@media screen and (max-width: 500px) {
    .uniq-part2 {

        grid-template-columns: repeat(1, minmax(100px, 1fr));
        max-width: 100%;
    }
}

