/* id-6 Upload pic css */

.upload-pic-main {
    max-width: 1140px;
    display: flex;
    gap: 2%;
}

.upload-pic-part1 {
    width: 28%;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    padding: 25px;
    display: flex;
    flex-direction: column;
    gap: 10px;

}

.upload-pic-part2 {
    width: 70%;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;
    padding: 25px;
    display: flex;
    flex-direction: column;
    gap: 10px;

}

.align-self-btns {
    align-self: flex-end;
}

.mb-0 {
    margin-bottom: 0px;
}

/* ID:6 Media Query */

@media screen and (max-width: 1100px) {
    .upload-pic-main {
        flex-direction: column;
        gap: 20px;
    }

    .upload-pic-part1 {
        width: 100%;
    }

    .upload-pic-part2 {
        width: 100%;
    }
}


/* Excel Modal css */

.excel-prev-main {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 60vw;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgb(209, 213, 219) 0px 0px 0px 1px inset;

    height: 300px;
}

.excel-prev-head {
    background-color: var(--color-orange);
    padding: 10px 5px 10px 5px;
    color: var(--color-white);
    font-size: 1rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
}

.excel-modal-close-icon {
    color: var(--color-white);
}